import React from "react";
import { TextField } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { withStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import Logo from "../../assets/images/logo.png";
import closeLock from "../../assets/images/awesome-lock.svg";
import ProfileIcon from "../../assets/images/awesome-user-circle.svg";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import "./login.scss";
import Typography from "@material-ui/core/Typography";
import * as Actions from "../../store/User/UserAction";
import History from "../../routes/History";
import ReactGA from "react-ga";

const GreenCheckbox = withStyles({
  root: {
    color: "#04724D",
    "&$checked": {
      color: "#04724D",
    },
  },
  checked: {},
})((props) => <Checkbox color='default' {...props} />);

class Login extends React.Component {
  state = {
    email: "",
    emailError: "",
    userName: "",
    password: "",
    passwordError: "",
    userNameError: "",
    checkedG: false,
  };

  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;

    if (prev.isLoggedIn !== cur.isLoggedIn && cur.isLoggedIn == true) {
      History.push("/dashboard");
    }
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
  }

  handleChecked = (event) => {
    this.setState({
      checkedG: event.target.checked,
    });
  };

  handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  handleChange = (e, name) => {
    this.setState({ [name]: e.target.value });
  };

  validateFields = () => {
    let validity = true;

    const email = this.state.email;
    const validEmail = RegExp(
      /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i
    );

    const password = this.state.password;

    if (email === "" || !validEmail.test(email)) {
      this.setState({ emailError: "Enter valid email" });
      validity = false;
    } else {
      this.setState({ emailError: "" });
    }
    if (password === "") {
      this.setState({ passwordError: "Please Enter password" });
      validity = false;
    } else {
      this.setState({ passwordError: "" });
    }

    return validity;
  };

  validateFile = () => {};

  submitClick = () => {
    const { email, password } = this.state;
    let payload = {
      email: email,
      password: password,
    };
    if (this.validateFields()) {
      this.props.UserAction.login(payload);
    }
  };

  render() {
    return (
      <div className='login-background'>
        <div className='logo-container'>
          <img src={Logo} style={{ height: "10%", width: "10%" }} />
        </div>
        <div className='login-text-main'>
          <div className='login-heading'>
            <Typography>LogIn</Typography>
          </div>
          <p className='para-text'>For Admin</p>
        </div>
        <div className='login-main-container'>
          <div className='loginform-div'>
            <div className='text-field-input'>
              <TextField
                id='input-with-icon-textfield'
                placeholder='Name'
                className='inputClass'
                value={this.state.email}
                onChange={(e) => this.handleChange(e, "email")}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position='start'>
                      <img src={ProfileIcon} className='lock-img' />
                    </InputAdornment>
                  ),
                }}
              />
              <div>
                <small style={{ color: "red", paddingLeft: "6px" }}>
                  {this.state.emailError}
                </small>
              </div>
            </div>
          </div>

          <div className='loginform-div'>
            <div className='text-field-input'>
              <TextField
                id='input-with-icon-textfield'
                placeholder='Password'
                type='password'
                value={this.state.password}
                onChange={(e) => this.handleChange(e, "password")}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position='start'>
                      <img src={closeLock} className='lock-img' />
                    </InputAdornment>
                  ),
                }}
              />
              <div>
                <small style={{ color: "red", paddingLeft: "6px" }}>
                  {this.state.passwordError}
                </small>
              </div>
            </div>
          </div>
          <div className='stay-login'>
            <FormControlLabel
              control={
                <GreenCheckbox
                  checked={this.state.checkedG}
                  onChange={this.handleChecked}
                  name='checkedG'
                />
              }
              label='Stay Logged In'
            />
          </div>

          <div className='button-div'>
            <Button
              variant='contained'
              className='Button'
              onClick={() => this.submitClick()}
              style={{
                textTransform: "none",
                fontSize: "20px",
                fontWeight: "500",
                borderRadius: "15px",

                padding: "5px 40px",
              }}
            >
              Confirm
            </Button>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Login));
